import { getClockTime, toISOString } from '../utils/dates';
export default class Session {
    constructor() {
        this.info = {
            started: false,
            horarioInicio: '',
            data: ''
        };
    }
    start() {
        const date = new Date();
        this.info.started = true;
        this.info.horarioInicio = getClockTime(date);
        this.info.data = toISOString(date);
    }
    end() {
        this.info.started = false;
        this.info.horarioInicio = '';
        this.info.data = '';
    }
    isSet() {
        return this.info.started;
    }
}
Session.IID = 'SessionService';
